.actors {
  display: grid;
  max-width: calc(100vw - 48px);
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  grid-gap: 16px;
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style: none;
  margin-left: auto;
  margin-right: auto;
}
.actor {
  list-style-type: none;
  font-size: 12px;
}
.name {
  font-weight: 600;
}
.profile {
  width: 100px;
}
